import {
  Tooltip,
  notification,
  Form,
  Input,
  Button,
  Spin,
  message,
} from 'antd';
import React, { useState } from 'react';
import { UserOutlined, SendOutlined } from '@ant-design/icons';
import animation from '../../assets/Animations/forgotPassword.png';
import axios from 'axios';

const ForgetPassword = () => {
  const [spin, setSpin] = useState(false);
  const [form] = Form.useForm();

  const onFinish = (values) => {
    setSpin(true);
    axios
      .post('https://ginnovation-server.herokuapp.com/api/forgotpassword', {
        email: values.email,
      })
      .then((resp) => {
        setSpin(false);
        console.log(resp);
        if (resp.data.status === 'success') {
          message.success('Request Successfull');
          openNotification('bottomRight');
        } else {
          message.warning('Opss! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        message.error('Ouch! An error occured');
      });
  };
  const openNotification = (placement) => {
    const args = {
      message: `Email Sent Successfully!`,
      description:
        'A email will apear in your inbox/spam after a while. Please click on the Reset Password button to reset your password',
      duration: 0,
      placement,
    };
    notification.open(args);
  };
  return (
    <div className='forget-password-page'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='container'>
        <div>
          <header>
            <h2>Forgot Your Password ?</h2>
            <p>No worries, you can reset it by entering your email below</p>
            <div className='input-div'>
              <Form
                form={form}
                className='my-form'
                name='resetPassword'
                onFinish={onFinish}
              >
                <Form.Item
                  name='email'
                  rules={[
                    {
                      type: 'email',
                      message: 'The input is not valid E-mail!',
                    },
                    {
                      required: true,
                      message: 'Please input your E-mail!',
                    },
                  ]}
                >
                  <Input
                    prefix={<UserOutlined className='site-form-item-icon' />}
                    placeholder='Your Email'
                  />
                </Form.Item>

                <div className='form-btn-row'>
                  <div className='register-btn-container'>
                    <Tooltip
                      placement='bottomRight'
                      title='This will send a reset link to your mail'
                    >
                      <Button type='primary' htmlType='submit'>
                        <SendOutlined /> Send
                      </Button>
                    </Tooltip>
                  </div>
                </div>
              </Form>
            </div>
          </header>
          <img src={animation} alt='forgot password' />
        </div>
      </div>
    </div>
  );
};

export default ForgetPassword;
